.app_contact-wrapper{
  display: flex;
  background-color: #e5e6eb;
  align-items: center;
  align-content: center;
  flex-direction: column;
  height: auto;
  padding-top: 15vh;
  padding-bottom: 10vh;

  h4{
    align-self: centers;
    
    text-align: center;
  }

  
  .app__footer-form {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  
    div {
      width: 70vw;
      margin: 0.75rem 0;
      
      border-radius: 10px;
      cursor: pointer;
      background-color: var(--primary-color);
  
      transition: all 0.3s ease-in-out;
  
      input,
      textarea {
        width: 100%;
        padding: 0.95rem;
        border: none;
        border-radius: 7px;
        background-color: var(--primary-color);
  
        font-family: var(--font-base);
        color: var(--secondary-color);
        outline: none;
      }
  
      textarea {
        height: 170px;
      }
  
      &:hover {
        box-shadow: 0 0 25px var(--primary-color);
      }
    }
  
    button {
      padding: 1rem 2rem;
      border-radius: 10px;
      border: none;
      background-color: #3c3c80;
      align-self: center;
      font-weight: 500;
      color: white;
      outline: none;
      margin: 2rem 0 0 0;
      font-family: var(--font-base);
  
      transition: cubic-bezier(0.55, 0.085, 0.68, 0.53);
      cursor: pointer;
  
      &:hover {
        background-color: #2430af;
      }
    }
  
    @media screen and (max-width: 576px) {
      width: 100%;
      margin: 1rem 0;
    }
  }

  .app__footer-map {
      margin-top: 20px;
  }
}